.starting-page {
  min-height: 84vh;
}

.login-signIn-container {
  display: flex;
  flex-direction: column;  
  align-items: center;
  gap: 20px;
  margin: auto;
  width: 250px;
  margin-top: 100px;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px;
}

.login-signIn-container img {
  width: 150px;
}

.title {
  font-family: 'Cookie', cursive, 'Dancing Script', cursive;
  font-size: 40px;
}

.sign-up-text {
  width: 200px;
  text-align: center;
  font-size: 14px;
}

.label-container {
  border: 1px solid #8e8e8e;
  width: 200px;
  padding-left: 5px;
  border-radius: 5px;
  padding-bottom: 2px;
}

.label-container input {
  border:0;
  outline:0;
  width: 190px;
}

.label {
  color: #8e8e8e;
  font-size: 12px;
}

form button {
  width: 207px;
  background-color: #0095f6;
  padding: 5px 0;
  border: 0;
  color: white;
  font-weight: 500;
  cursor: pointer;
} 

.signIn-button {
  margin-top: 15px;
}

.error {
  color: red;
  font-size: 12px;
  line-height: 1.1;
  width: 200px;
  height: 27px;
}

.login-error {
  height: 10px;
  color: red;
  font-size: 12px;
  margin-bottom: 3px;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    transition: background-color 5000s ease-in-out 0s;
}